.answer {
    -webkit-box-shadow: inset 0 0 50vh var(--a2fg);
            box-shadow: inset 0 0 50vh var(--a2fg);
}

.answer:hover {
    -webkit-box-shadow: inset 0 0 50vh var(--a2bg);
            box-shadow: inset 0 0 50vh var(--a2bg);
    cursor: pointer;
}

.answer.incorrect.selected {
    -webkit-text-decoration-line: line-through;
            text-decoration-line: line-through;
    opacity: 0.5;
    pointer-events: none;
}



